import React, { Component } from 'react';
import ViewSDKClient from '../ViewSDKClient';
import PropTypes from 'prop-types';
import CustomPdfActions from "../CustomPdfActions";
import SidebarTable from "../SidebarTable";
import {defaultTheme, lightTheme, Provider} from '@adobe/react-spectrum';

/**
 * PDF component using the Adobe Embed API. 
 * Takes file url location and json annotations.
 */
export default class DashPdfComponents extends Component {

    constructor() {
        super();
        this.state = {
            annotationManager: undefined,
            documentTable: undefined,
            highlightsDict: undefined,
            selectedText: "",
            drawerOpen: false,
            pdfRendered: "",
            showImpact: false
        };
    }

    viewerConfig = {
        /* Enable commenting APIs */
        enableAnnotationAPIs: true,  /* Default value is false */
        // includePDFAnnotations: true, // If true, annotations are saved to PDF buffer
        embedMode: "FULL_WINDOW",
        showDownloadPDF: false
    };

    setAnnotationManager = annotManager => {
        this.setState({
            annotationManager: annotManager
        });
    }

    loadPdf = () => {

        if (this.props.pdfRendered == "") {
            console.log("empty load" + this.props.pdfRendered);
            return;
        }

        ////// load the embed API script
        // const url = "https://documentcloud.adobe.com/view-sdk/main.js";
        // const script = document.createElement("script");
        // script.src = url;
        // script.async = true;
        // document.body.appendChild(script);

        let fileUrl = this.state.documentTable[this.props.pdfRendered].fileUrl;
        let fileName = this.state.documentTable[this.props.pdfRendered].fileName;
        let fileId = this.props.pdfRendered;
        let highlightsArr = this.state.documentTable[this.props.pdfRendered].highlightsArr;

        const viewSDKClient = new ViewSDKClient();
        this.state.viewSDKClient = viewSDKClient;
        viewSDKClient.ready().then(() => {
            /* Invoke the file preview and get the Promise object */
            this.state.previewFilePromise = viewSDKClient.previewFile(
                this.props.id,
                this.viewerConfig,
                this.props.apiKey,
                fileUrl,
                fileName,
                fileId
            );

            /* Use the annotation manager interface to invoke the commenting APIs */
            this.state.previewFilePromise.then(adobeViewer => {

                adobeViewer.getAPIs().then(apis => {
                    viewSDKClient.registerEventsHandler(apis);
                });

                adobeViewer.getAnnotationManager().then(annotManager => {

                    this.setAnnotationManager(annotManager);
                    if (highlightsArr.length != 0) {
                        console.log("adding annotations");
                        annotManager.addAnnotations(highlightsArr)
                        // annotManager.addAnnotationsInPDF(highlightsArr)            
                            .then(result => {
                                console.log("Success!! Annotations added", result);
                            })
                            .catch(error => {
                                console.log(error);
                            });
                    }
                    /* Set UI configurations */
                    const customFlags = {
                        showToolbar: false,   /* Default value is true */
                        showCommentsPanel: false,  /* Default value is true */
                        downloadWithAnnotations: true,  /* Default value is false */
                        printWithAnnotations: true,  /* Default value is false */
                    };
                    this.state.annotationManager.setConfig(customFlags);

                });
            });
        });

        console.log("Loading: " + this.props.pdfRendered);
    }

    componentDidMount() {
        this.loadPdf();
    }

    updateAnnotationReview = (id, status) => {
        let newDict = Object.assign({}, this.state.highlightsDict);
        newDict[id].isReviewed = true;
        newDict[id].isCorrect = status;

        let newDocTable = Object.assign({}, this.state.documentTable);
        newDocTable[this.props.pdfRendered].highlightsArr[0].creator.isCorrect = status;
        newDocTable[this.props.pdfRendered].highlightsArr[0].creator.isReviewed = true;
        
        this.setState({
            highlightsDict: newDict,
            documentTable: newDocTable
        });

        this.props.setProps({
            toggle: !this.props.toggle
        })

        console.log(this.state.documentTable[this.props.pdfRendered].highlightsArr[0].creator);
        console.log(this.props.documentTable);

    }

    addAnnotation = (newArr) => {

        // Add annotation 
        // index annot of pdfRendered 
        // add to document table

    }

    updatePdfRendered = (id) => {
        this.props.setProps({
            pdfRendered: id,
            key: id,
            toggle: !this.props.toggle
        });
        this.loadPdf()
    }

    setIsOpen = () => {
        
    }

    render() {

        const {
            key,
            id,
            label,
            apiKey,
            pdfRendered,
            documentTable,
            toggle
        } = this.props;

        // console.log(this.props.pdfRendered);
        // console.log(documentTable[0].contractId);
        // console.log(documentTable[1].data);

        let dict = {};
        documentTable.forEach(element => dict[element.contractId] = element.data);
        this.state.documentTable = dict;

    
        if (this.props.pdfRendered != "") {
            // Create a dictionary mapping each unique annotation id to category and name
            let highlightsDict = {};
            let highlightsArr = this.state.documentTable[this.props.pdfRendered].highlightsArr;
            highlightsArr.forEach(element => highlightsDict[element.id] = element.creator);
            this.state.highlightsDict = highlightsDict;
        }

        return (

            <Provider theme={lightTheme} colorScheme="light">
                <div className="pdf-view-container">
                    
                    <SidebarTable width="size-3000" 
                        documentTable={this.state.documentTable}
                        updatePdfRendered={this.updatePdfRendered}
                        showImpact={this.state.showImpact}
                    />
                    
                    <div id={id} className="pdf-view full-window-div"></div>
                    
                    {this.state.annotationManager &&
                        <CustomPdfActions
                            annotationManager={this.state.annotationManager}
                            highlightsDict={this.state.highlightsDict}
                            viewSDKClient={this.state.viewSDKClient}
                            updateAnnotationReview={this.updateAnnotationReview}
                            addAnnotation={this.addAnnotation}
                        />
                    }

                </div>
            </Provider>
        );
            

    }

}

DashPdfComponents.defaultProps = {
    id: "",
    label: "",
    apiKey: "",
    pdfRendered: "",
};

DashPdfComponents.propTypes = {

    /**
     * The ID used to identify this component in Dash callbacks.
     */
    key: PropTypes.string,

    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * A label that will be printed when this component is rendered.
     */
    label: PropTypes.string,

    /**
     * Api key for authentication
     */
    apiKey: PropTypes.string,

    /**
     * URL to file that will be displayed
     */
    pdfRendered: PropTypes.string,

    /**
     * URL to file that will be displayed
     */
    currPdf: PropTypes.string,

    /**
     * Annotations that will appear as highlights
     */
    documentTable: PropTypes.array,

    toggle: PropTypes.bool,

    showImpact: PropTypes.bool,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,

};